export default {
  data() {
    return {
      seller: [],
      search: {
        firstname: '',
        lastname: '',
        email: '',
        phone: '',
        label: '',
        gender: '',
        country: '',
        state: '0',
      },
      currentPage: 1,
      pageSize: 10,
      total: 0,
    }
  },
  async fetch() {
    const searchData = await this.$axios.$get('/api/admin/seller/search', {
      params: {
        ...this.search,
        currentPage: this.currentPage,
        pageSize: this.pageSize,
      },
    })
    this.total = searchData.data.total
    this.seller = searchData.data.list
  },
  methods: {
    async onSubmit(currentPage) {
      const searchData = await this.$axios.$get(
        '/api/admin/seller/search',
        {
          params: {
            ...this.search,
            currentPage: currentPage,
            pageSize: this.pageSize,
          },
        }
      )
      this.total = searchData.data.total
      this.seller = searchData.data.list
    },
    edit(index, row) {
      // console.log(index, row)
      this.$router.push(
        this.localePath(`/user/seller/create?sellerId=${row.sellerId}`)
      )
    },
    async agree(index, row) {
      // console.log(index, row)
      // setState
      const seller = await this.$axios.$post(
        '/api/admin/seller/setState',
        {
          sellerId: row.sellerId,
          state: 1,
        }
      )
      // console.log('seller', seller)
      this.seller.splice(index, 1)
    },
    async reject(index, row) {
      // console.log(index, row)
      // setState
      const seller = await this.$axios.$post(
        '/api/admin/seller/setState',
        {
          sellerId: row.sellerId,
          state: 2,
        }
      )
      // console.log('seller', seller)
      this.seller.splice(index, 1)
    },
    formatterDate(row, column, cellValue, index) {
      return this.$moment(cellValue).format('YYYY-MM-DD HH:mm:ss')
    },
    changeCurrentPage(currentPage) {
      this.onSubmit(currentPage)
    },
  },
}
